class Calculator {
  add(op1, op2) {
    return op1 + op2;
  }

  sub(op1, op2) {
    return op1 - op2;
  }

  mul(op1, op2) {
    return op1 * op2;
  }

  div(op1, op2) {
    return op1 / op2;
  }
}

export default Calculator;



